.app__aboutus {
    position: relative;
}

.app__aboutus-overlay {
    position: absolute;
    inset: 0;
}

.app__aboutus-overlay img {
    width: 391px;
    height: 415px;
    z-index: 0;
}

.app__aboutus-content {
    position: relative;
    width: 100%;
    z-index: 1;
}

.app__aboutus-p {
    color: var(--color-grey);
    margin: 2rem 0;
}

.app__aboutus-content_knife {
    margin: 2rem 4rem;
}

.app__aboutus-content_knife img{
    width: 84px;
    height: 703px;
}

.app__aboutus-content_about {
    flex: 1;
    display: flex;
    align-items: flex-end;
    flex-direction: column;
    text-align: right;
}

.app__aboutus-content_history {
    flex: 1;
}

.app__aboutus-content_about .spoon__img {
    transform: rotate(180deg);
}

@media screen and (max-width: 2000px){
    .app__aboutus-p {
        margin: 4rem 0;
    }
}

@media screen and (max-width: 900px){
    .app__aboutus-content {
        flex-direction: column;
    }

    .app__aboutus-content_knife {
        margin: 4rem 0;
    }
}

@media screen and (max-width: 650px){
    .app__aboutus-overlay img {
        width: 80%;
        height: 320px;
    }
}